/* 
README
   p* 为四个方向都加统一数值 例:p10 都加10px 
   p*_* 上下一个数值 左右一个数值 例：p10_15 上下10px 左右15px
   ptb* 上下 例:ptb10 上下加10px
   plr* 左右 例:plr10 左右加10px
   pt*  上 例：pt10 上加10px
   pb*  下 例：pb10 下加10px
   pl*  左 例：pl10 左加10px
   pr*  右 例：pr10 右加10px

   m* 为四个方向都加统一数值 例:m10 都加10px 
   m*_* 上下一个数值 左右一个数值 例：m10_15 上下10px 左右15px
   mtb* 上下 例:mtb10 上下加10px
   mlr* 左右 例:mlr10 左右加10px
   mt*  上 例：mt10 上加10px
   mb*  下 例：mb10 下加10px
   ml*  左 例：ml10 左加10px
   mr*  右 例：mr10 右加10px

   size*  字体大小 例：size15  = font-size:15px
   
   bold 字体加粗
   
   w_*  百分比宽度  例:w_5 = width:5%
   w*  px宽度  例:w5 = width:5px
   min_w* 最小宽度百分比 例: min_w50 = min-width:50%
   max_w* 最大宽度百分比 例：max_w50 = max-width:50%
   h_*  百分比高度  例:h_5 = height:5%
   h*  px高度  例:h5 = height:5px

   img*  宽高统一数值的 class 例：img15  = width:15px height:15px;

   m_center = margin:0 auto;

   wx_color  微信绿 #1aad19

   t_center  文字居中  =  text-align:center;

